.loggin{
    color: pink;
    margin-left: 60%;
    width: 21vw;
    height: 90%;
    display: block;
    margin: auto;
    
}

.Emmail{
    cursor: pointer;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #d8d6de;
    border-radius: 0.357rem;
    border-radius: 7px;
    height: 5vh;
    /* transition: 0.1s; */
    width: 20vw;
}


.Emmail:hover {
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #9480d1;
    border-radius: 0.357rem;
    border-radius: 7px;
    /* transition: 0.1s; */
    width: 20vw;
  }
  .Emmail:focus {
    outline: 1px solid #9480d1;
    translate: 0.1s;
    box-shadow: 1px 4px 1px 1px rgba(0, 0, 255, .2);
    transition: 0.2s ease-in-out;
    /* box-shadow: 0 1 3pt red; */
    border-radius: 7px;
    /* transition: 0.1s; */
    width: 20vw;
    /* transition: 0.1s;  oranges! yey */
  }

  .bttttn {
